<!--BoxTitle组件doc-->
<!--
实用方式：  参考demo
<BoxTitle title="标题">
 <div class="content">content</div>
</BoxTitle>

说明：标题默认是路由mate.title字段的值
-->
<template>
    <el-card class="box-card">
        <template v-slot:header>
            <div v-if="label" class="clearfix">
                <span>{{label}}</span>
            </div>
            <slot name="header"></slot>
        </template>
        <slot></slot>
    </el-card>
</template>
<script>
    export default {
        name: 'BoxTitle',
        props: {
            title: {
                type: String,
                default: '',
            }
        },
        data() {
            return {}
        },
        computed: {
            pageTitle() {
                return this.$route.meta ? this.$route.meta.title : '';
            },
            label() {
                return this.title ? this.title : this.pageTitle
            }
        }
    };
</script>
<style scoped lang="scss">
    .box-card {
        width: 100%;
        position: relative;

        /deep/ .search {
            padding: 0;
        }

        /deep/ .el-card__header {
            padding: 0.16rem 0.13rem 0.16rem 0.4rem;
            position: relative;
            font-family: 'PFC';
            font-size: 16px;
        }

        /deep/ .el-card__header:after {
            content: "";
            position: absolute;
            background: #2e4af7;
            left: 0.24rem;
            top: 0.16rem;
            width: 0.06rem;
            height: 0.2rem;
        }
    }
</style>