<!--电费暂估表-->
<template>
    <div class="box">
        <div class="print-btn">
            <el-button type="primary" v-show="freezeStatus" @click="freeze">冻结</el-button>
            <el-button type="primary" v-print="'#table-content'">打 印</el-button>
            <el-button type="default" @click="html2Excel">导 出</el-button>
        </div>
        <div id="table-content" ref="dom">
            <div class="item">
                <div class="scorll">
                    <table class="table">
                        <thead>
                            <tr class="noBorder head">
                                <td style="text-align:center" colspan="16">西藏先锋绿能环保科技有限公司</td>
                            </tr>
                            <tr class="noBorder head">
                                <td style="text-align:center" colspan="16">电费暂估表</td>
                            </tr>
                            <tr class="noBorder">
                                <td colspan="5" style="text-align:left">制表单位：西藏先锋绿能环保科技有限公司</td>
                                <td colspan="5">{{date}}</td>
                                <td style="text-align:right" colspan="6">金额单位：元</td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <th>序号</th>
                                <th>物资名称</th>
                                <th>客户</th>
                                <th>业主单位</th>
                                <th>用电性质</th>
                                <th>计量单位</th>
                                <th>数量</th>
                                <th>预估单价</th>
                                <th>昆仑管理费</th>
                                <th>昆仑预存电量45%</th>
                                <th>含税金额</th>
                                <th>税率</th>
                                <th>不含税金额</th>
                                <th>原因</th>
                                <th>预估入账月份</th>
                                <th>冲回月份</th>
                            </tr>
                            <tr v-for="(tr,index) in list">
                                <td>{{index+1}}</td>
                                <td>{{tr.resourceName}}</td>
                                <td>{{tr.customer}}</td>
                                <td>{{tr.kcShortName}}</td>
                                <td>{{tr.wkTy}}</td>
                                <td>{{tr.unit}}</td>
                                <td>{{tr.amount}}</td>
                                <!--数量-->
                                <td>{{tr.provisionalPrice}}</td>
                                <!--预估单价-->
                                <td>{{tr.klMgtCharge}}</td>
                                <!--昆仑管理费-->
                                <td>{{tr.klPrestore}}</td>
                                <!--昆仑预存电量45-->
                                <td>{{tr.taxAmount}}</td>
                                <!--含税金额-->
                                <td>{{tr.taxRate}}%</td>
                                <!--税率-->
                                <td>{{tr.noTaxAmount}}</td>
                                <!--不含税金额-->
                                <td>{{tr.reason}}</td>
                                <!--原因-->
                                <td>{{tr.provisionalMonth}}</td>
                                <!--预估入账月份-->
                                <td>{{tr.chonghuiMonth}}</td>
                                <!--冲回月份-->
                            </tr>
                            <tr>
                                <td colspan="5">合计</td>
                                <td>度</td>
                                <td>{{sumDl}}</td>
                                <!--数量-->
                                <td></td>
                                <!--预估单价-->
                                <td></td>
                                <!--昆仑管理费-->
                                <td></td>
                                <!--昆仑预存电量45-->
                                <td>{{sumPrice}}</td>
                                <!--含税金额-->
                                <td></td>
                                <!--税率-->
                                <td>{{sumPriceNoRate}}</td>
                                <!--不含税金额-->
                                <td></td>
                                <!--原因-->
                                <td></td>
                                <!--预估入账月份-->
                                <td></td>
                                <!--冲回月份-->
                            </tr>
                            <tr class="noBorder">
                                <td colspan="3" style="text-align:center">业务部门负责人：</td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td style="text-align:center" colspan="3">打印人：{{personName}}</td>
                            </tr>
                            <tr class="noBorder">
                                <td colspan="16" style="text-align:left">
                                    <span class="desc-lf" v-show="!!des">备注：</span>
                                    <input class="desc-input" type="text" v-model="des" placeholder="输入备注">
                                    <span class="desc-print">{{des}}</span>
                                </td>
                            </tr>
                        </tbody>

                    </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    //组件
    import {
        mapState
    } from 'vuex'
    import BoxTitle from "@/components/BoxTitle.vue";
    import {
        parseTime,
        formatMoneyAddComma,
        getMaxFixedLen
    } from "@/utils/index.js"
    // import Table from "./Table.vue";
    // import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getZgQuery,
        getZgGenerate,
        getZgBillFreeze,
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import Table2Excel from 'table2excel.js'
    export default {
        // extends: Index, //继承通用index
        name: 'useEqManage',
        components: {
            BoxTitle,
            // SearchInputs,
            // Table
        },
        data() {
            return {
                month: '',
                list: [],
                sumDl: 0,
                sumPrice: 0,
                sumPriceNoRate: 0,
                showInput: false,
                des: '',
                freezeStatus: false,

            }
        },
        computed: {
            ...mapState({
                personName: state => state.user.nickName,
            }),
            date() {
                return parseTime(this.month, '{y}年{m}月27日')
            }
        },
        mounted() {
            let style = document.createElement('style')
            style.setAttribute("type", "text/css");
            style.setAttribute("id", "tableStyle");
            style.innerHTML = `
                <!--
                    @page {
                        margin:1.54cm 2cm;               
                    }
                -->
            `;
            let el = this.$refs.dom;
            el.appendChild(style);
            if (el.attachEvent) { // IE
                el.attachEvent('onmousewheel', this.mouseScroll.bind(el))
            } else if (el.addEventListener) {
                el.addEventListener('DOMMouseScroll', this.mouseScroll.bind(el), false)
                el.onmousewheel = this.mouseScroll.bind(el)
            }
        },
        methods: {
            parseTime,
            init(month) {
                this.month = month;
                this._getData();
            },
            _getData() {
                getZgGenerate({
                    month: this.month,
                }).then(() => {
                    return getZgQuery({
                        month: this.month,
                        _loading: false, //不显示加载中图标
                    })
                }).then(res => {
                    this.list = res.rows ? res.rows : [];
                    this.sumDl = 0;
                    this.sumPrice = 0;
                    this.sumPriceNoRate = 0;
                    //合计
                    this.list.map(item => {
                        // item.amount = formatMoneyAddComma(item.amount)
                        this.sumDl += item.amount;
                        this.sumPrice += item.taxAmount;
                        this.sumPriceNoRate += item.noTaxAmount;
                        item.amount = formatMoneyAddComma(item.amount, 2)
                        item.taxAmount = formatMoneyAddComma(item.taxAmount)
                        item.noTaxAmount = formatMoneyAddComma(item.noTaxAmount)
                    })
                    this.list[0] && (this.freezeStatus = !tool.isYes(this.list[0].freeze));
                    // this.sumDl = this.sumDl;
                    // console.log(this.sumPrice);
                    // console.log(this.sumPriceNoRate);
                    this.sumDl = formatMoneyAddComma(this.sumDl, 2);
                    this.sumPrice = formatMoneyAddComma(this.sumPrice);
                    this.sumPriceNoRate = formatMoneyAddComma(this.sumPriceNoRate);
                })
            },
            freeze() {
                this.$confirm('冻结后表格数据将存入历史库且不可修改，是否确认操作？', '冻结数据', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    getZgBillFreeze({
                        month: this.month,
                    }).then(res => {
                        this._getData();
                    })
                }).catch(() => {

                })
            },
            //下载excel
            html2Excel() {
                new Table2Excel('#table-content table').export(
                    `先锋绿能电费暂估表-${this.parseTime(this.month,'{y}年{m}月')}`);
            },
            //刷新数据
            reloadTable() {
                this._getData();
            },
            //鼠标滚动
            mouseScroll(event) { // google 浏览器下
                let detail = event.wheelDelta || event.detail;
                let moveForwardStep = -1;
                let moveBackStep = 1;
                let step = 0;
                step = detail > 0 ? moveForwardStep * 100 : moveBackStep * 100;
                event.preventDefault(); // 阻止浏览器默认事件
                this.$refs.dom.querySelector('.scorll').scrollLeft = this.$refs.dom.querySelector(
                    '.scorll').scrollLeft + step;
            }
        }
    }
</script>
<style scoped lang="scss">
    @media print {
        #table-content {
            zoom: 85%;
            margin-top: 0 !important;
        }

        .head {
            font-size: 18px;
            font-weight: 600;
        }

        .scorll {
            overflow: hidden;
            padding-right: 2px;

        }


        .desc-icon,
        .desc-input {
            display: none !important;
        }

        .desc-print {
            display: inline-block !important;
        }

        .table-h {
            color: #000;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        th,
        span {
            font-size: 9pt !important;
            padding: 40px 0px !important;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        span {
            color: #000;
        }

        .table-footer,
        .table-h-list,
        .table-h {
            line-height: 0.3in !important;
        }

        .left {
            margin-left: 3in !important;
        }

        .right {
            margin-right: 2in !important;
        }



        table {
            page-break-inside: avoid;
        }

    }



    #table-content {
        margin-top: 50px;
    }



    .scorll {
        overflow: auto;
        padding-bottom: 10px;
        padding-right: 2px;
    }

    .noBorder {
        td {
            border: 0 !important;
        }

        &:hover {
            background-color: transparent !important;
        }
    }

    .box {
        padding-top: 24px;
        position: relative;

        .print-btn {
            position: absolute;
            right: 0;
            top: 24px;
        }

        .desc-print {
            display: none;
        }

        .desc-input {
            border: 0;
            outline: none;
            color: #303133;
            font-size: 14px;
            font-family: "PFM" !important;
        }
    }

    th,
    td {
        border: 1px solid #d3d3d3;
    }

    th,
    td {
        text-align: center;
        padding: 10px 10px;
        white-space: nowrap;
    }

    tr:nth-child(even) {
        // background-color: #f2f2f2;
    }

    .table {
        border-collapse: collapse;
        width: 100%;
    }

    .item {
        margin-bottom: 16px;

        .table-h {
            font-size: 16px;
            text-align: center;
            line-height: 40px;

        }

        .table-h-list {
            line-height: 40px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;

            span {
                text-align: left;

                &:last-child {
                    text-align: right;
                }
            }
        }

        .table-footer {
            display: flex;
            justify-content: space-between;
            line-height: 40px;

            .left {
                margin-left: 150px;
            }

            .right {
                margin-right: 100px;
            }
        }
    }
</style>