<!--table 父类-->
<script>
    import {
        debounce
    } from "@/utils/index.js"
    export default {
        name: 'TableCommon',
        data() {
            return {
                dialogShow: false,
                loading: false,
                //表单
                tableData: [], //表格数据
                total: 0, //总数
                page: { // 分页参数
                    pageNum: 1, //当前页数
                    pageSize: 10,
                },
                search: {}, // 搜索条件
            }
        },
        mounted() {
            //获取数据
            if (this._getData) {
                this._getData = debounce(this._getData.bind(this), 100)
                this._getData(this.page);
            }
        },
        methods: {
            handleClose() {
                this.dialogShow = false;
            },
            //表格分页条数变化
            handleSizeChange(data) {
                if (this.page.pageSize !== data) {
                    this.page.pageSize = data;
                    this._getData();
                }
            },
            //表格分页页数
            handleCurrentChange(data) {
                if (this.page.pageNum !== data) {
                    this.page.pageNum = data;
                    this._getData();
                }
            },
            //刷新表格数据
            reloadTable() {
                this._getData();
            },
            //搜索按钮
            searchHand(data) {
                this.page.pageNum = 1; //搜索时页数置为1
                this.search = data;
                this._getData();
            }, //操作按钮点击的事件
        },
        activated: function () {
            // 移除任何你不想保留的数据
            this._getData();
        },
        deactivated: function () {
            // 移除任何你不想保留的数据
            this.tableData = [];
        }
    }
</script>