<!--用电申请-->
<template>
    <div class="box">
        <div class="item">
            <InfoBox :items="infoIems" title="异常账单统计"></InfoBox>
        </div>

        <el-table v-table-scroll row-key="index" :data="tableData"
            :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column label="序号" prop="index" width="90" fixed="left" />
            <el-table-column label="所属平台" prop="kpName" width="200"></el-table-column>
            <el-table-column label="用电性质" prop="workType" width="90"></el-table-column>
            <el-table-column label="账单月份" prop="arSnum" width="120">
                <template slot-scope="scope">
                    <span>{{ changeMonth(scope.row['month']) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="起度" prop="arSnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arSnum']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有起止度-->
                </template>
            </el-table-column>
            <el-table-column label="止度" prop="arEnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arEnum']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有起止度-->
                </template>
            </el-table-column>
            <el-table-column label="倍率" prop="blRate" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['blRate']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有倍率-->
                </template>
            </el-table-column>
            <el-table-column label="电量（kW·h）" prop="useEq" width="120">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['useEq']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有倍率-->
                </template>
            </el-table-column>
            <el-table-column label="分摊损耗比例" prop="loseRate" width="120"></el-table-column>
            <el-table-column label="损耗电量" prop="loseEq" width="100"></el-table-column>
            <el-table-column label="补录电量" prop="extraEq" width="100"></el-table-column>
            <el-table-column label="总电量（kW·h）" prop="allEq" width="130"></el-table-column>
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="所属路线" prop="glName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip width="250"></el-table-column>
            <el-table-column label="用能单位" prop="sdName" show-overflow-tooltip width="240"></el-table-column>
            <el-table-column label="结算起止码时间" prop="ts" width="300">
                <template slot-scope="scope">
                    <span v-if="scope.row['arSt']">
                        {{changeTime(scope.row['arSt'])}}-{{changeTime(scope.row['arEt'])}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" fixed="right" class-name="cusTable" width="100">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <span class="cus-btn" @click="handClick('预估电量',scope.row)" type="text" size="small">预估电量</span>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="page.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="page.pageSize"
                layout="prev, pager, next, jumper,total, sizes" :total="total">
            </el-pagination>
        </div>
        <!--弹窗编辑-->
        <el-dialog title="添加预估电量" custom-class="cusDialog" :visible.sync="this.dialogShow" width="4.6rem"
            :before-close="()=>{this.dialogShow = false}">
            <el-form :model="formData" :rules="rule" ref="form" class="form">
                <div class="li">
                    <div class="item">
                        <el-form-item>
                            <div class="label">公司名称：</div>
                            <div class='rt'>
                                <div class="box">{{detailData.kcName}}</div>
                            </div>
                        </el-form-item>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <el-form-item>
                            <div class="label">平台名称：</div>
                            <div class='rt'>
                                <div class="box">{{detailData.kpName}}</div>
                            </div>
                        </el-form-item>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <el-form-item>
                            <div class="label">用电单位：</div>
                            <div class='rt'>
                                <div class="box">{{detailData.sdName}}</div>
                            </div>
                        </el-form-item>
                    </div>
                </div>

                <div class="li">
                    <div class="item">
                        <el-form-item prop="estimateEq">
                            <div class="label"><span class="important">*</span>预估电量：</div>
                            <div class='rt'>
                                <div class="box">
                                    <el-input type="number" min="0" v-model="formData.estimateEq" placeholder="请输入预估电量">
                                        <template slot="append">kW·h</template>
                                    </el-input>
                                </div>
                            </div>
                        </el-form-item>
                    </div>
                </div>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">确 定</el-button>
                <el-button @click="()=>{this.dialogShow = false}">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        getZgNoEq,
        postZgBillFix
    } from "@/api/archives.js";
    import {
        parseTime
    } from "@/utils/index";
    import TableCommon from "@/components/TableCommon.vue";
    import InfoBox from "@/components/InfoBox.vue";
    //公用函数
    import tool from "@/utils/tool.js";
    export default {
        extends: TableCommon, //继承通用index
        name: 'editDataTable',
        components: {
            InfoBox,
        },
        data() {
            return {
                infoIems: [],
                loading: false,
                dialogShow: false,
                formObj: {},
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '90px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                //表单生成规则
                rule: {
                    estimateEq: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('预估电量不能为空'));
                            } else if (value <= 0) {
                                callback(new Error('请输入大于0的数字'));
                            } else if (value != Number(value) || value === '0.') {
                                callback(new Error('请输入合法数字'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur'
                    }],
                },
                detailData: {},
                formData: {
                    estimateEq: 0, //预估电量
                },
                month: '', //当前月份
            }
        },
        methods: {
            init(month) {
                this.month = month;
                this._getData();
            },
            handClick(btnName, row) {
                if (btnName === '预估电量') {
                    this.detailData = row;
                    this.dialogShow = true;
                    this.formData.estimateEq = this.detailData.allEq ? this.detailData.allEq : 0;
                }
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}年{m}月')
            },
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}')
            },
            submit() { //提交
                postZgBillFix({
                    "billId": this.detailData.id,
                    "estimateEq": this.formData.estimateEq
                }).then(res => {
                    this.dialogShow = false;
                    this.$message.success('操作成功');
                    this.detailData = {};
                    this.formData.estimateEq = 0;
                    this.$refs.form.clearValidate();
                    this.reloadTable();
                })
            },
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getZgNoEq({
                    ...this.page,
                    month: this.month
                }).then(res => {
                    const data = res.rows ? res.rows : [];
                    //处理数据序号
                    let gwNum = 0;
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { //国网账单 加特殊标识
                            gwNum++;
                            item.index += ' 【国】';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                    this.infoIems = [{
                        label: '当前账单总数',
                        num: this.total,
                    }, {
                        label: '国网账单数',
                        num: gwNum,
                    }, {
                        label: '非国网账数',
                        num: this.total - gwNum,
                    }]
                }).catch(err => {
                    this.tableData = [];
                    this.total = 0;
                })
            },

        }
    }
</script>
<style scoped lang="scss">
    .box {
        .item {
            padding-bottom: 24px;
        }

        padding-top: 24px;
        position: relative;

        .print-btn {
            position: absolute;
            right: 20px;
            top: 24px;
        }

        .cus-btn {
            color: #2E4AF7;
            font-size: 14px;
            padding-left: 25px;
            cursor: pointer;
            line-height: 22px;
            position: relative;

            &:first-child {
                padding-left: 0;
            }
        }

        .table-btns {
            text-align: center;

            .cus-btn {
                padding: 0;
            }

            .line:after {
                content: "";
                position: absolute;
                display: block;
                right: -13px;
                top: 6px;
                bottom: 0;
                height: 12px;
                width: 1px;
                background: #E3E5EC;
            }

            .center {
                width: 100%;
            }

        }
    }

    .form {
        .li {

            display: flex;
            margin-bottom: 22px;

            /deep/ .el-form-item {
                margin-bottom: 0;
            }

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;
                padding-bottom: 0;

                .label {
                    font-family: "PFC";
                    min-width: 0.8rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;
                    color: #666;
                    padding: 0 10px;
                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }
    }
</style>