<template>
    <el-card class="box-card">
        <div class="month-div">
            <span>月份： </span>
            <el-date-picker v-model="month" type="month" format="yyyy年M月" :picker-options="pickerBeginDateBefore"
                placeholder="选择月" @change="change">
            </el-date-picker>
        </div>
        <keep-alive>
            <el-tabs v-model="activeName" @tab-click="tabClick">
                <el-tab-pane label="电费暂估表" name="0">
                    <Assess ref="0"></Assess>
                </el-tab-pane>
                <el-tab-pane label="异常数据维护" name="1">
                    <EditDataTable ref="1"></EditDataTable>
                </el-tab-pane>
            </el-tabs>
        </keep-alive>
    </el-card>
</template>

<script>
    import Assess from "./assess.vue";
    import EditDataTable from "./editDataTable.vue";

    import {
        getPrevMonth,
        parseTime,
    } from "@/utils/index.js"
    export default {
        name: 'index',
        components: {
            Assess,
            EditDataTable,
        },
        data() {
            return {
                activeName: '0',
                month: getPrevMonth(26),
                pickerBeginDateBefore: {
                    disabledDate(time) {
                        return time.getTime() >= (Date.now());
                    }
                }
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            change() {
                this.init()
            },
            init() {
                let monthStr = parseTime(this.month, '{y}-{m}');
                this.$refs[0].init(monthStr);
                this.$refs[1].init(monthStr);
            },
            tabClick({
                index
            }) {
                this.$refs[index] && this.$refs[index].reloadTable();
            }
        }
    }
</script>

<style scoped lang="scss">
    .box-card {
        width: 100%;
        position: relative;

        /deep/ .el-card__body {
            padding: 0 0.24rem 0.24rem 0.24rem;
        }

        .month-div {
            position: absolute;
            right: 24px;
            top: 12px;
            z-index: 2;

            /deep/ .el-date-editor {
                width: 1.4rem;
            }
        }
    }
</style>